.widgets{
    flex:0.3;
    min-width:fit-content;
    overflow: scroll;
    padding: 10px 10px;
}

.widgets::-webkit-scrollbar{
    display: none;
}

.widgets_input {
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px 20px;
    border-radius: 50px;
    margin-top: 10px;
    margin-left: 20px;
  }
  
  .widgets_input>input {
    border: none;
    margin-top: 0px;
    background-color: var(--twitter-background);
  }

.widgets_searchIcon{
    color: gray;
}

.widgets_widgetContainer{
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background-color: #f5f8fa;
    border-radius: 20px;
}

.widgets_widgetContainer>h2{
    font-size: 18px;
    font-weight: 800;
}

@media screen and (max-width=800) {
    .widgets{
        display: none;
    }
}
